import SearchIcon from "@mui/icons-material/Search";
import {
  Box,
  Button,
  Chip,
  CircularProgress,
  Modal,
  TablePagination,
  TableSortLabel,
  Typography,
} from "@mui/material";
import IconButton from "@mui/material/IconButton";
import InputBase from "@mui/material/InputBase";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { BaseUrl } from "../../constant/BaseUrl";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { FilterContext } from "../../context/FilterContext";
import DescTooltip from "../../component/DescTooltip";

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === "desc"
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

const headCells = [
  {
    id: "parent_item",
    label: "Finished Good",
  },
  {
    id: "total_order_quantity",
    label: "Total Customer Orders",
  },
  {
    id: "Quantity_yet_to_make",
    label: "Quantity to build",
  },
  {
    id: "previousCTB",
    label: "Previous CTB",
  },
  {
    id: "ctb",
    label: "Updated CTB",
  },
  {
    id: "lastUpdated",
    label: "Last Modified",
  },
];

function EnhancedTableHead(props) {
  const { order, orderBy, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead sx={{ fontWeight: "600" }}>
      <TableRow>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            sortDirection={orderBy === headCell.id ? order : false}
            align="center"
            sx={{
              color: "#404040",
              fontFamily: "Montserrat, sans-serif",
              fontWeight: "600",
              fontSize: "12px",
              padding: "8px",
              borderRight: "1px solid #9F9F9F",
              backgroundColor: "#E5F1F8",
              "&:last-child td, &:last-child th": { borderRight: 0 },
            }}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : "asc"}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

const RecentScenarios = () => {
  const { filterState, setFilterState } = useContext(FilterContext);
  const [order, setOrder] = useState("desc");
  const [orderBy, setOrderBy] = useState("lastUpdated");
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const history = useHistory();
  const [searchValue, setSearchValue] = useState("");
  const [finishedGoodData, setFinishedGoodData] = useState([]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  useEffect(() => {
    axios
      .get(
        `${BaseUrl}${filterState.location}/get-snap-FG/${filterState.username}`
      )
      .then((res) => {
        // console.log(res.data);
        setFinishedGoodData(res?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const filteredFG = finishedGoodData.filter((fgitem) =>
    fgitem?.parent_item.match(new RegExp(searchValue, "i"))
  );

  const goToScenarioPlanner = (item) => {
    setFilterState({ ...filterState, snapSelectedFG: item });
    history.push("/recent-scenarios-fg");
  };

  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          backgroundColor: "#fff",
          position: "fixed",
          top: 64,
          left: 65,
          right: 0,
          zIndex: "9",
          padding: "15px 20px",
        }}
      >
        <div style={{ flex: 5 }}>
          <Typography
            component="div"
            sx={{
              flexGrow: 1,
              fontSize: "25px",
              color: "#0077B6",
              fontWeight: "600",
            }}
          >
            Recent Scenarios
          </Typography>
        </div>
        <div
          style={{
            flex: 3,
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
            gap: "20px",
          }}
        >
          <Paper
            sx={{
              p: "1px 2px",
              display: "flex",
              alignItems: "center",
              width: 250,
              border: "1px solid #E4E4E4",
              boxShadow: "none",
            }}
          >
            <InputBase
              sx={{ ml: 1, flex: 1 }}
              placeholder="Search Finish Goods"
              inputProps={{ "aria-label": "search Finish Goods" }}
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
            />
            <IconButton type="button" sx={{ p: "5px" }} aria-label="search">
              <SearchIcon />
            </IconButton>
          </Paper>
        </div>
      </div>
      <div>
        <Paper
          sx={{
            width: "100%",
            overflow: "hidden",
            boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 5px",
            borderRadius: "5px",
          }}
        >
          <TableContainer sx={{ maxHeight: 450 }}>
            <Table stickyHeader aria-label="sticky table">
              <EnhancedTableHead
                order={order}
                orderBy={orderBy}
                onRequestSort={handleRequestSort}
              />
              <TableBody>
                {stableSort(filteredFG, getComparator(order, orderBy))
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row, index) => (
                    <TableRow hover key={row?.parent_item}>
                      <TableCell
                        align="center"
                        onClick={() => goToScenarioPlanner(row?.parent_item)}
                        sx={{
                          minWidth: "200px",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          fontSize: "15px",
                          fontWeight: "600",
                          letterSpacing: "1px",
                          color: "#0077B6",
                          textDecoration: "underline",
                          cursor: "pointer",
                        }}
                      >
                        <DescTooltip data={row?.description}>
                          <p>{row?.parent_item}</p>
                        </DescTooltip>
                      </TableCell>
                      <TableCell
                        align="center"
                        sx={{
                          borderLeft: "1px solid #9F9F9F",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          minWidth: "150px",
                        }}
                      >
                        {row?.total_order_quantity}
                      </TableCell>
                      <TableCell
                        align="center"
                        sx={{
                          borderLeft: "1px solid #9F9F9F",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          minWidth: "150px",
                        }}
                      >
                        {row?.Quantity_yet_to_make}
                      </TableCell>
                      <TableCell
                        align="center"
                        sx={{
                          borderLeft: "1px solid #9F9F9F",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          minWidth: "150px",
                        }}
                      >
                        {row?.previousCTB}
                      </TableCell>
                      <TableCell
                        align="center"
                        sx={{
                          borderLeft: "1px solid #9F9F9F",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          minWidth: "150px",
                        }}
                      >
                        {row?.ctb}
                      </TableCell>
                      <TableCell
                        align="center"
                        sx={{
                          borderLeft: "1px solid #9F9F9F",
                          borderBottom: "1px solid #E4E4E4",
                          padding: "8px",
                          minWidth: "150px",
                        }}
                      >
                        {row?.lastUpdated}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
            {finishedGoodData.length === 0 && (
              <div
                style={{
                  display: "flex",
                  flex: 1,
                  alignItems: "center",
                  justifyContent: "center",
                  height: "200px",
                }}
              >
                <CircularProgress size="100px" sx={{ color: "#185ABC" }} />
              </div>
            )}
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25, 50, 100]}
            component="div"
            labelRowsPerPage="Finish goods per page"
            count={filteredFG.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Paper>
      </div>
    </>
  );
};

export default RecentScenarios;
